#organization {
	width: 100%;
	.inner {
		.aboutSectionTitle {
			text-transform: uppercase;
			font-size: 20px;
			font-weight: 400;
			padding-bottom: 30px;
			span {
				font-family: $koFont;
				font-size: 16px;
				color: $textColor;
			}
		}
		.aboutSectionCont {
			width: 100%;
			.organizationWarp {
				width: 100%;
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				.organizationList {
					position: relative;
					width: calc(100% / 3);
					height: 600px;
					padding: 30px;
					border: 1px solid $borderColor;
					border-bottom: none;
					margin-left: -1px;
					min-height: 500px;
					.organizationBox {
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						width: 100%;
						height: 100%;
						.organizationTitle {
							@include sectionSubTitle($subFz: 18px, $subLetterS: 3px);
							text-transform: uppercase;
							margin-bottom: 10px;
						}
						.organizationImg {
							width: 100%;
							height: 80%;
							display: flex;
							align-items: center;
							justify-content: center;
							margin-bottom: 10px;
							img {
								width: 60%;
								height: 60%;
								object-fit: cover;
								filter: grayscale(0.7);
								transition: 0.5s;
							}
							&:hover {
								img {
									filter: grayscale(0.4);
									transform: scale(1.1);
								}
							}
						}
						.organizationJob {
							position: relative;
							@include sectionText($captionFz: 16px);
							&::after {
								content: '';
								display: block;
								clear: both;
							}
							span {
								float: right;
							}
						}
					}
					&:nth-of-type(1) .organizationBox .organizationImg {
						background: rgba(161, 154, 151, 0.6);
					}
					&:nth-of-type(2) .organizationBox .organizationImg {
						background: rgba(164, 161, 152, 0.6);
					}
					&:nth-of-type(3) .organizationBox .organizationImg {
						background: rgba(155, 161, 151, 0.6);
					}
					&:nth-of-type(4) .organizationBox .organizationImg {
						background: hsl(160, 5%, 64%, 0.6);
					}
					&:nth-of-type(5) .organizationBox .organizationImg {
						background: hsl(207, 6%, 61%, 0.6);
					}
					&:nth-of-type(6) .organizationBox .organizationImg {
						background: hsla(245, 6%, 61%, 0.6);
					}

					&::after {
						position: absolute;
						bottom: 0;
						left: 10%;
						content: '';
						width: 80%;
						height: 1px;
						background: $borderColor;
					}
					&:nth-last-child(-n + 3) {
						border-top: none;
					}
					&:nth-of-type(1),
					&:nth-of-type(4) {
						border-left: none;
					}
					&:nth-of-type(3),
					&:nth-of-type(6) {
						border-right: none;
					}
					&.intro {
						.organizationBox {
							position: relative;
							.organizationTitle {
								@include sectionTitle($fz: 40px, $fontWeight: bold);
								margin-bottom: 40px;
							}
							.organizationSub {
								@include sectionSubTitle($subFz: 24px, $fontWeight: 400);
							}
							.organizationCaption {
								@include sectionText;
							}
						}
					}
				}
			}
		}
	}
}
#location {
	width: 80%;
	margin-left: 20%;
	#map {
		//width: 80%;
		height: 400px;
		border: 1px solid $borderColor;
		filter: saturate(0%) invert(100%);
	}
	.mapTxt {
		width: 60%;
		margin-top: 20px;
		p {
			@include sectionText;
		}
	}
	.mapBtn {
		display: flex;
		align-items: flex-start;
		margin-top: 30px;
		.mapBtnTitle {
			width: 15%;
			@include sectionText($captionFz: 16px, $captionFontColor: $textColor);
		}
		.mapBtnLists {
			width: 85%;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			.mapBtnList {
				width: 49%;
				margin-right: 2%;
				padding: 20px 0;
				border-top: 2px solid $hoverColor;
				margin-bottom: 10px;
				.listBox {
					.listTitle {
						@include sectionText($captionFz: 14px, $captionFontColor: $textColor);
						margin-bottom: 30px;
					}
					.traffic {
						display: flex;
						align-items: center;
						.trafficList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.trafficList + .trafficList {
							margin-left: 20px;
						}
					}
					.roadView {
						display: flex;
						align-items: center;
						.roadViewList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.roadViewList + .roadViewList {
							margin-left: 20px;
						}
					}
					.favLocation {
						display: flex;
						align-items: center;
						.locaList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.locaList + .locaList {
							margin-left: 20px;
						}
					}
				}
				&:nth-of-type(even) {
					margin-right: 0;
				}
				&:nth-of-type(3) {
					width: 100%;
					margin-right: 0;
				}
			}
		}
	}
}

