			padding-top: 60px;
			.subContents {
				border: 1px solid $borderColor;
				border-top: none;
				.subHeader {
					position: relative;

					> .inner {
						width: 100%;
						.subHeaderMenu {
							float: left;
							ul {
								display: flex;
								align-items: center;
								li + li {
									margin-left: 50px;
								}
							}
						}
					}
				}
				.subSection {
					.sectionExp {
						padding-bottom: 40px;
					}
				}
			}
		}
	}
}
