		.wrap {
			.subContents {
				.subHeader {
					> .inner {
						padding: 50px 0 0 0;
						.subHeaderMenu {
							ul {
								flex-wrap: wrap;
								li {
									a {
										@include sectionText($captionFz: 14px, $captionFontColor: #333);
									}
									&:hover {
										a {
											@include sectionText($captionFz: 14px, $captionFontColor: $hoverColor);
										}
									}
									&.on {
										a {
											@include sectionText($captionFz: 14px, $captionFontColor: $hoverColor);
										}
									}
								}
								li + li {
									margin-left: 20px;
								}
							}
						}
					}
				}
				.subSection {
					.sectionExp {
						.expTitle {
							@include sectionSubTitle($subFz: 16px, $fontWeight: 400);
						}
						.expCaption {
							@include sectionText($captionFz: 14px);
						}
					}
				}
			}
		}
	}
}
